.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.f-label {
  font-size: 24px;
}

.f-button {
  color: white;
  background: black;
}

.f-textinput {
  color: white;
  background: black;
  font-size: 16px;
}

.f-radio-item {
  margin-right: 12px;
  font-size: 14px;
}

.f-formitem {
  display: flex;
  flex-direction: column;
  margin-left: 24px;
  margin-top: 24px;
}

.f-formitem .f-icon {
  margin-left: 12px;
}

.f-formitem-label {
  display: flex;
  flex-direction: column;
  font-size: 10px;
}

.f-hint {
  height: 20px;
}

.f-hint .f-label {
  font-size: 12px;
}

.f-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  margin: 10px;
  width: 50%;
}

.f-form input[type='submit'] {
  color: white;
  background: black;
  font-size: 20px;
  margin: 14px;
}

.f-picture-panel {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
}

.f-picture-panel .f-button,
.f-picture-panel img {
  margin-top: 20px;
}

.f-picture-panel .f-button {
  font-size: 20px;
}

.f-image-left {
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
  filter: invert(100%);
}

.f-linear-list {
  display: flex;
  align-items: center;
  overflow-x: auto;
  width: 90%;
  border: 1px solid white;
  padding: 10px;
  margin: 10px;
}

.f-linear-list-item {
  display: flex;
  align-items: center;
}

.f-form-error .f-label {
  color: red;
  font-size: 14px;
}
